.WeatherMap{height:100%;width:100%;color:#fff;background:#004e89;background:-moz-linear-gradient(135deg,#004e89 0,#1a659e 100%);background:-webkit-linear-gradient(135deg,#004e89 0,#1a659e 100%);background:linear-gradient(135deg,#004e89 0,#1a659e 100%);display:grid;grid-template-columns:1fr 2fr 1fr;grid-template-rows:.1fr 2fr 1fr;align-items:center}.WeatherMap button{grid-row:1/2;grid-column:2/3;border:none;border-radius:10px;height:80%;width:80px;font-weight:400}