.Splash{height:100%;width:100%;color:#fff;background:#004e89;background:-moz-linear-gradient(135deg,#004e89 0,#1a659e 100%);background:-webkit-linear-gradient(135deg,#004e89 0,#1a659e 100%);background:linear-gradient(135deg,#004e89 0,#1a659e 100%);display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr 1fr 1fr;grid-row-gap:30px;grid-column-gap:0;align-items:center;justify-items:center}.Splash .Title{grid-column:2/3;grid-row:2/3;text-align:center;font-size:6em}.Splash .Instructions{grid-column:2/3;grid-row:3/4;text-align:center;font-size:2em}.Splash .Input{grid-column:2/3;grid-row:4/5;width:100%;display:grid;grid-template-columns:1fr;grid-template-rows:1fr;align-items:center;justify-items:center}.Splash .Input input{width:100%;font-family:Roboto,sans-serif;font-size:2em;font-weight:300;padding:10px;margin:10px;text-align:center;border:none;border-radius:10px;box-shadow:1px 1px 10px 3px #a9a9a9 inset}